.translation-history {
  padding: 35px 0;
  min-height: 100dvh;
  // .translation-history__head

  &__head {

  }

  // .translation-history__title

  &__title {
    display: block;
    margin-bottom: 25px;
  }

  // .translation-history__info

  &__info {
  }

  // .translation-history__content-text

  &__content-text {
    display: block;
    margin-bottom: 10px;
  }

  // .translation-history__content

  &__content {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 32px;
  }

  // .translation-history__block

  &__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    background: rgba(255, 189, 34, 0.18);
    border-radius: 12px;
    padding: 10px;
  }

  // .translation-history__item

  &__item {
    display: flex;
    align-items: center;
  }

  // .translation-history__country

  &__country {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }

  // .translation-history__country-img

  &__country-img {
    width: 35px;
    height: 35px;
  }

  // .translation-history__country-name

  &__country-name {
    font-weight: 500;
    font-size: 8px;
    line-height: 108%;
    text-align: center;
    color: #000;
  }

  &__status{
    width: 21px;
    height: 21px;
  }

  // .translation-history__money

  &__money {
    font-weight: 600;
    font-size: 13px;
    line-height: 108%;
    color: #000;
  }

  // .translation-history__money-value

  &__money-value {
    font-weight: 400;
    font-size: 13px;
    line-height: 108%;
    color: #000;
  }

  &__bg{
    position: absolute;
    left: -100px;
    top: 100px;
  }

  &__name{

  }
}
.container {
}
.logo {
  display: block;
  margin: 0 auto;
  margin-bottom: 20px;
}
.text-3 {
  font-weight: 500;
  font-size: 15px;
  line-height: 108%;
  color: #000;
}
.name {
}
.text-1 {
  font-weight: 700;
  font-size: 32px;
  line-height: 108%;
  color: #000;
}
.text-2 {
  font-weight: 600;
  font-size: 17px;
  line-height: 108%;
  color: #000;
}
.translation-btn {
  font-size: 21px;

}
.btn-1 {
  font-weight: 700;
  font-size: 15px;
  line-height: 108%;
  text-align: center;
  color: #fff;
  padding: 12px;
  border-radius: 7px;
  background: #000;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 108%;
  height: 49px;
}

.select-page {
  min-height: 100dvh;
  padding-top: 25px;
  padding-bottom: 50px;
  // .select-page__head

  &__head {
    display: flex;
    flex-direction: column;
  }

  // .select-page__pagination

  &__pagination {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 25px;
  }

  // .select-page__back

  &__back {
    font-weight: 500;
    font-size: 15px;
    line-height: 108%;
    color: #fff;
    background: #ffbd22;
    border-radius: 6px;
    padding: 4px 15px;
  }

  // .select-page__pagination-num

  &__pagination-num {
    font-weight: 500;
    font-size: 10px;
    line-height: 108%;
    color: #fff;
    padding: 3px 6px;
    background: #ffbd22;
    border-radius: 5px;
  }

  // .select-page__search-label

  &__search-label {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #000;
    border-radius: 30px;
    padding: 12px 22px;
    margin-bottom: 20px;
  }

  // .select-page__search-input

  &__search-input {
    font-weight: 500;
    font-size: 12px;
    line-height: 108%;
    color: #000;
    width: 85%;
    &::placeholder{
      font-size: 12px;
    }
  }

  // .select-page__search-btn

  &__search-btn {
  }

  // .select-page__list

  &__list {
    display: flex;
    flex-direction: column;
    gap: 17px;
  }

  // .select-page__value

  &__value {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 45px;
    background: rgba(255, 189, 34, 0.17);
    position: relative;
    height: 45px;
  }

  // .select-page__value-img

  &__value-img {
    position: absolute;
    left: 0;
    top: 0;
  }

  // .select-page__value-text

  &__value-text {
    font-weight: 600;
    font-size: 17px;
    line-height: 108%;
    text-align: center;
    color: #000;
  }
}

.select-page {

  // .select-page__currency

  &__currency {
    padding: 13px 20px;
    background: rgba(255, 189, 34, 0.18);
    border-radius: 45px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
  }

  // .select-page__currency-value

  &__currency-value {
    font-weight: 700;
    font-size: 17px;
    line-height: 108%;
    text-align: center;
    color: #000;

    &--absolute{
      position: absolute;
      left: 15px;
      top: 50%;
      transform: translateY(-50%);
    }
    &--medium{
      font-weight: 500;
    }
  }

  // .select-page__currency-value2

  &__currency-value2 {
    font-weight: 600;
    font-size: 17px;
    line-height: 108%;
    text-align: center;
    color: #000;
  }
		// .select-page__translation

		&__translation {
      margin-top: 95px;
      position: relative;
      display: flex;
      flex-direction: column;
      gap: 5px;
      margin-bottom: 10px;
      &::after{
        display: block;
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 450px;
        height: 160px;
        filter: blur(100px);
        background-color: #FFBD22;
        z-index: -1;
      }
		}

    &__translation-arrow{
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: 2;
    }

		// .select-page__translation-item

		&__translation-item {
      display: flex;
      flex-direction: column;
      border-radius: 12px;
      background: rgba(255, 255, 255, 0.36);
      padding: 10px 10px 14px 16px;
      position: relative;
		}

		// .select-page__translation-text

		&__translation-text {
      font-weight: 700;
      font-size: 10px;
      line-height: 108%;
      color: #000;
		}

		// .select-page__translation-value

		&__translation-value {
      font-weight: 400;
      font-size: 25px;
      line-height: 108%;
      color: #000;
      background-color: transparent;
      max-width: 200px;
      display: block;
      outline: transparent;
      &::placeholder{
        opacity: 0.5;
      }
		}

		// .select-page__translation-departure

		&__translation-departure {
      font-style: italic;
      font-weight: 600;
      font-size: 15px;
      line-height: 108%;
      color: #000;
      position: absolute;
      right: 10px;
      bottom: 10px;
		}

		// .select-page__translation-course

		&__translation-course {
      font-weight: 500;
      font-size: 11px;
      line-height: 108%;
      color: #000;
      display: block;
      margin-bottom: 90px;
      text-align: center;
		}

		// .select-page__translation-info

		&__translation-info {
      display: flex;
      flex-direction: column;
      gap: 14px;
      margin-bottom: 38px;
		}

		// .select-page__translation-info-text

		&__translation-info-text {
      font-weight: 600;
      font-size: 15px;
      line-height: 108%;
      color: #000;
		}

		// .select-page__translation-info-p

		&__translation-info-p {
      font-weight: 500;
      font-size: 9px;
      line-height: 135%;
      color: #000;
		}

		// .select-page__translation-link

		&__translation-link {
		}

}

.recipients-details-page{
  &::after{
    display: block;
    content: "";
    width: 100%;
    height: 300px;
    filter: blur(100px);
    background-color: rgba(255, 207, 94, 0.76);
    position: absolute;
    top: -150px;
    z-index: -1;
  }
}

.recipients-details {
  margin-bottom: 95px;
  &__item{
    padding: 16px;
    background: rgba(255, 189, 34, 0.3);
    border-radius: 12px;

  }

  // .recipients-details__name

  &__name {
    font-weight: 600;
    font-size: 10px;
    line-height: 108%;
    color: #000;
  }

  // .recipients-details__input

  &__input {
    font-weight: 400;
    font-size: 13px;
    line-height: 108%;
    color: #000;
    background-color: transparent;
