.data-page{
  overflow: hidden;
  position: relative;
  &::after{
    display: block;
    content: "";
    position: absolute;
    top: -100px;
    right: -200px;
    width: 450px;
    height: 160px;
    filter: blur(80px);
    background-color: #FFBD22;
    z-index: -1;
  }
}

.complety-page{
  overflow: hidden;
  position: relative;
  padding-top: 80px;

  &::after{
    display: block;
    content: "";
    position: absolute;
    top: -100px;
    right: -200px;
    width: 450px;
    height: 160px;
    filter: blur(80px);
    background-color: #FFBD22;
    z-index: -1;
  }
  &__img{
    display: block;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 80px;
  }
  &__text{
    font-weight: 400;
    font-size: 11px;
    line-height: 180%;
    text-align: center;
    color: #000;
    display: block;
    margin-bottom: 100px;
  }
}

